	const app = getApp()
	import MescrollMixin from '../componets/mescroll-uni/mescroll-mixins.js';
	export default {
		mixins: [MescrollMixin], // 使用mixin
	data() {
		return {
			Inv: 0,
			imgUrl: '',
			navId: '-1',
			nav: [{ navName: '全部', id: '-1' }, { navName: '待付款', id: '1' }, { navName: '待发货', id: '2' }, { navName: '待收货', id: '3' }, { navName: '评价', id: '4' }],
			list: [],
			downOption: {
				auto: true //是否在初始化后,自动执行downCallback; 默认true
			},
			upOption: {
				page: {
					num: 0, // 当前页码,默认0,回调之前会加1,即callback(page)会从1开始
					size: 20 // 每页数据的数量,默认10
				},
				noMoreSize: 5, // 配置列表的总数量要大于等于5条才显示'-- END --'的提示
				empty: {
					tip: '暂无相关数据'
				},
				textNoMore: '没有更多数据了'
			},
			orderType: ''
		};
	},
	onLoad(e) {
		this.imgUrl = this.$helper.imgUrl
		console.log(e);
		if(e.type == '图书订单'){
			this.orderType = 'book'
		}else{
			this.orderType = 'data'
		}
		uni.setNavigationBarTitle({
			title: e.type
		});
	},
	methods: {
		// 选项卡点击事件
		navTap(e, id) {
			this.Inv = e;
			this.navId = id;
			this.list = []
			this.mescroll.resetUpScroll();
		},
		prompEvaluate(e){
			uni.navigateTo({
				url:`/pages/orderAssess?obj=${JSON.stringify(e)}&type=${this.orderType}`
			})
		},
		/*下拉刷新的回调 */
		downCallback() {
			this.mescroll.resetUpScroll();
		},
		upCallback(page) {
			this.getList(page)
		},
		getList(page){
			uni.showLoading({
				title: '加载中...',
			})
			uni.request({
				url: `${this.$helper.requestUrl}user/myOrderList`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data:{
					type: this.orderType,
					state: this.navId,
					page_size: page.size,
					page: page.num
				},
				success: res => {
					uni.hideLoading()
					res = this.$helper.null2str(res.data);
					if (res.status_code == 'ok') {
						if(res.message == '暂无信息'){
							this.list = []
							this.mescroll.endByPage(0, 0);
						}else{
							if (page.num == 1) this.list = [];
							this.list = this.list.concat(res.data.data);
							this.mescroll.endByPage(res.data.data.length,  res.data.total);
						}
					} else {
						
					}
				}
			});
		},
		// 再次购买
		againBuy(e){
			uni.navigateTo({
				url: `/pages/indexDataDetail?type=${this.orderType}&id=${e}`
			})
		},
		// 提醒发货
		remind(e){
			uni.showLoading({
				title: '加载中...',
			})
			uni.request({
				url: `${this.$helper.requestUrl}user/sendRemind`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data:{
					type: this.orderType,
					ordernum: e,
				},
				success: res => {
					uni.hideLoading()
					res = this.$helper.null2str(res.data);
					if (res.status_code == 'ok') {
						uni.showToast({
							title:res.message,
							icon: 'none'
						})
						setTimeout(e => {
							this.mescroll.resetUpScroll();
						}, 2000)
					} else {
						uni.showToast({
							title:res.message,
							icon: 'none'
						})
					}
				}
			});
		},
		// 确认收货
		orderConfirm(e){
			uni.showLoading({
				title: '加载中...',
			})
			uni.request({
				url: `${this.$helper.requestUrl}user/receive`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data:{
					type: this.orderType,
					ordernum: e,
				},
				success: res => {
					uni.hideLoading()
					res = this.$helper.null2str(res.data);
					if (res.status_code == 'ok') {
						uni.showToast({
							title:res.message,
							icon: 'none'
						})
						setTimeout(e => {
							this.mescroll.resetUpScroll();
						}, 2000)
					} else {
						uni.showToast({
							title:res.message,
							icon: 'none'
						})
					}
				}
			});
		},
		// 删除取消订单
		del(e){
			uni.showLoading({
				title: '删除中...',
			})
			uni.request({
				url: `${this.$helper.requestUrl}user/delOrder`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data:{
					type: this.orderType,
					ordernum: e,
				},
				success: res => {
					uni.hideLoading()
					res = this.$helper.null2str(res.data);
					console.log(res);
					if (res.status_code == 'ok') {
						uni.showToast({
							title:res.message,
							icon: 'none'
						})
						setTimeout(e => {
							this.mescroll.resetUpScroll();
						}, 2000)
					} else {
						uni.showToast({
							title:res.message,
							icon: 'none'
						})
					}
				}
			});
		},
		// 立即支付
		promptlyPay(e){
			uni.request({
				url: `${this.$helper.requestUrl}pay/pay`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					type: this.orderType,
					ordernum: e,
				},
				success: res => {
					res = this.$helper.null2str(res.data);
					console.log(res, '-------------------');
					uni.hideToast()
					if (res.status_code == 'ok') {
						this.getPay(res.data);
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
				}
			});
		},
		getPay(payNode){
			uni.showLoading({
				title: '支付加载中...',
			})
			uni.requestPayment({
				provider: 'wxpay',
				timeStamp: payNode.timeStamp,
				nonceStr: payNode.nonceStr,
				package: payNode.package,
				signType: payNode.signType,
				paySign: payNode.paySign,
				success: res => {
					uni.hideLoading()
					res = this.$helper.null2str(res)
					uni.showToast({
						title: "支付成功",
						icon: 'success',
						duration: 2000
					});
					// 两秒后返回上一页
					setTimeout(e => {
						this.mescroll.resetUpScroll();
					}, 2000)
				},
				fail: err => {
					uni.showToast({
						title: "支付失败",
						icon: 'none',
						duration: 2000
					});
				}
			});
		},
		goDetail(e){
			console.log(e)
			uni.navigateTo({
				url: `/pages/orderDetail?obj=${JSON.stringify(e)}&type=${this.orderType}`
			})
		}
		
	}
};
